import React from 'react';
import Layout from '../components/layout';

import './rsvp.css';

class RSVP extends React.Component {
  state = {
    rsvp: null,
    numAdults: 0,
    numChildren: 0,
    firstName: null,
    lastName: null,
  };

  render() {
    return (
      <Layout container="true" title="RSVP">
        <form>
          <div className="flexContainer">
            <div className="flexItem">
              <label className="required" htmlFor="firstName">First Name</label>
              <input onChange={(e) => this.setState({ firstName: e.target.value })} name="firstName" id="firstName" type="text" required />
            </div>
            <div className="flexItem">
              <label className="required" htmlFor="lastName">Last Name</label>
              <input onChange={(e) => this.setState({ lastName: e.target.value })} name="lastName" id="lastName" type="text" required />
            </div>
          </div>

          <div className="flexContainer">
            <div className="flexItem">
              <label className="required" htmlFor="rsvp">RSVP</label>

              <div className="radioGroup">
                <input onChange={() => this.setState({rsvp: 'yes'})} name="rsvp" id="yes" value="Accepts" type="radio" required />
                <label htmlFor="yes">Accepts</label>
              </div>

              <div className="radioGroup">
                <input onChange={() => this.setState({rsvp: 'no', numAdults: 0, numChildren: 0})} name="rsvp" id="no" value="Regrets" type="radio" required />
                <label htmlFor="no">Regrets</label>
              </div>
            </div>
          </div>


                    {
            this.state.rsvp == 'yes' ? 
            <div>
              {
                this.state.numAdults + this.state.numChildren > 0 ? 
                <div className="flexContainer">
                  <div className="flexItem">
                    <p>Total guests: {this.state.numAdults + this.state.numChildren}</p>
                  </div>
                </div>
                :
                ''
              }

              <div className="flexContainer">
                <div className="flexItem">
                  <label className="required" htmlFor="numAdults">Number of Adults Attending</label>
                  <input onChange={(e) => this.setState({ numAdults: Number(e.target.value)})} name="numAdults" id="numAdults" type="number" pattern="\d*" required />
                </div>
                <div className="flexItem">
                  <label className="required" htmlFor="numChildren">Number of Children Attending</label>
                  <input onChange={(e) => this.setState({ numChildren: Number(e.target.value)})} name="numChildren" id="numChildren" type="number" pattern="\d*" required />
                </div>
              </div>

            </div>
            :
            ''
          }
        </form>
      </Layout>
    );
  }
}

export default RSVP;
